.module-mark-layout-posts{padding-top:45px}.module-mark-header{border:solid 1px #e8e8e8;background-color:white;display:flex;flex-direction:row;align-items:center;padding:10px;height:45px;position:fixed;top:50px;width:100%;z-index:100;border-bottom-left-radius:5px;border-bottom-right-radius:5px}@media screen and (min-width:576px){.module-mark-header{width:500px;margin:0 auto}}.module-mark-header-icon{display:flex;justify-content:center;width:30px}.module-mark-header-icon i{font-size:18px}.module-mark-header-text{margin-left:5px}.module-mark-empty{margin-top:10px;height:200px;display:flex;justify-content:center;align-items:center;background-color:white;border:solid 1px #e8e8e8;border-radius:5px}.module-mark-empty-text{color:rgba(0,0,0,0.65)}.module-mark-active-mark{color:#eac600}